.Navigation {
	position: fixed;
	top: 0;
	z-index: 1;
	width: 100%;
	box-shadow: 0 0 10px #FF6D00;
	font-weight: bold;
	color: #FF6D00;
  background: #000000;
}

.major-nav-list {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	width: 100%;
	list-style-type: none;
}

.list-item {
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	height: 60px;
	padding: 0 30px;
	font-size: 20px;
}

.list-item:hover {
	cursor: pointer;
	background: #FF6D00;
	color: #000000;
}

.minor-nav-list {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	-webkit-align-items: center;
	        align-items: center;
	list-style-type: none;
}

.nav-header {
	margin-right: auto;
	padding: 0 20px;
	font-family: 'LACETRIM';
	font-size: 20px;
	font-weight: normal;
	text-transform: uppercase;
}

@media only screen and (max-width: 600px) {
  .major-nav-list {
  	-webkit-flex-direction: column;
  	        flex-direction: column;
  }

  .list-item {
  	height: 40px;
  	font-size: 18px;
  }

  .major-nav-list li {
  	-webkit-justify-content: center;
  	        justify-content: center;
  	width: 100%;
  }

  .nav-header {
		padding: 0;
	}
}
.ShowcaseImage {
	position: absolute;
	top: 0;
  left: 0;
  right: 0;
  height: 450px;
  overflow: hidden;
  background-image: url(/static/media/halloween-showcase-large.de667fe7.jpg);
	background-size: cover;
	background-position: center center;
}

@media only screen and (min-width: 1500px) {
	.ShowcaseImage {
		height: 600px;
	}
}

@media only screen and (max-width: 800px) {
	.ShowcaseImage {
		top: 50px;
		height: 350px;
		background-image: url(/static/media/halloween-showcase-small.fb9e83ff.jpg);
	}
}
.HomeSection {
	position: absolute;
	top: 450px;
	right: 0;
	left: 0;
}

.link-container {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
	-webkit-justify-content: center;
	        justify-content: center;
}

.link-button {
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	width: 350px;
	height: 60px;
	margin: 20px;
	border-radius: 10px;
	background: #FFFFFF;
	cursor: pointer;
	-webkit-transition: all 0.1s linear;
	transition: all 0.1s linear; 
}

.link-button:hover {
	box-shadow: 0 0 20px #FFFFFF;
}

.link-button:active {
	box-shadow: 0 0 5px #FFFFFF;
	-webkit-transform: scale(0.95);
	        transform: scale(0.95);
}

@media only screen and (max-width: 800px) {
	.HomeSection {
		top: 400px;
	}

	.link-button {
		margin: 10px;
	}
}

@media only screen and (min-width: 1500px) {
	.HomeSection {
		top: 600px;
	}
}

.UnknownProduct {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	background: #FFFFFF;
}

.question-mark {
	height: 150px;
}
.Products {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
	-webkit-justify-content: center;
	        justify-content: center;
	margin-top: 90px;
}

.rounded-section {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-align-items: center;
	        align-items: center;
  width: 80%;
  margin-bottom: 20px;
  border-right: 2px solid #000000;
  border-bottom: 2px solid #000000;
  border-radius: 5px;
  box-shadow: 5px 5px 10px #000000;
  background: #FF6D00;
}

.product-container {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
	-webkit-justify-content: center;
	        justify-content: center;
	padding: 10px;
}

.product {
	height: 200px;
	width: 130px;
	margin: 10px;
	border-radius: 3px;
	cursor: pointer;
	-webkit-transition: all 0.1s linear;
	transition: all 0.1s linear; 
}

.product:hover {
	-webkit-transform: scale(1.05);
	        transform: scale(1.05);
}

.product:active {
	-webkit-transform: scale(0.95);
	        transform: scale(0.95);
}

@media only screen and (max-width: 600px) {
	.Products {
		margin-top: 110px;
	}
}
#SummaryPage {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
}

.product-container {
	margin-top: 20px;
	width: 75%;
}

.product-body {
	margin-top: 10px;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
	-webkit-justify-content: center;
	        justify-content: center;
}

.product-image {
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
}

.product-image img {
	height: 350px;
}

.product-summary {
	margin: 20px 0;
	font-size: 17px;
}

.product-keywords {
	font-size: 17px;
	color: #FF6D00;
}

@media (min-width: 800px) {
	.product-container {
		margin-top: 10px;
	}

	.product-body {
		display: grid;
		grid-gap: 30px;
		grid-template-columns: 1fr auto;
	}

	.product-image {
		border-right: 5px solid #FFFFFF;
		padding-right: 50px;
		grid-row: span 2;
	}

	.product-summary {
		grid-row: span 2;
		margin: 0;
	}

	.product-keywords {
		grid-column: span 2;
	}
}

@media (min-width: 1000px) {
	.product-body {
		grid-template-columns: auto 1fr;
		grid-template-rows: auto 1fr;
	}

	.product-image {
		padding-right: 100px;
	}

	.product-summary, .product-keywords {
		grid-row: span 1;
		grid-column: span 1;
		font-size: 19px;
	}
}
.About {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
